import React, { SyntheticEvent, useCallback } from 'react';

interface Props {
  src: string;
  transformAudioURL?: (src: string) => string;
  onMediaLoadSuccess: (event: SyntheticEvent<HTMLAudioElement, Event>) => void;
  onMediaLoadError: (event: SyntheticEvent<HTMLAudioElement, Event>) => void;
  isMobile?: boolean;
}

function AudioPlayer(props: Props) {
  const { src, transformAudioURL, onMediaLoadError, onMediaLoadSuccess, isMobile } = props;
  const trueSrc = transformAudioURL ? transformAudioURL(src) : src;

  const handleAudioClick = useCallback((e) => {
    if (isMobile) {
      e.stopPropagation();
    }
  }, [isMobile]);

  return (
    <audio
      controls
      controlsList="nodownload nofullscreen noremoteplayback"
      onCanPlay={onMediaLoadSuccess}
      onError={onMediaLoadError}
      onClick={handleAudioClick}
      onMouseDown={handleAudioClick}
    >
      <source src={trueSrc} type="audio/mp3" />
    </audio>
  );
}

export default AudioPlayer;
